@import "_normalize";
@import "_variables";

:root{
  font-size: 1em;
  height: auto;
  min-height: 100%;
  position: relative;
}

body {
  //look at class .body
}

.about-me{
  display: flex;
  flex-direction: column;
  &__header{
    color: $dark-blue;
    margin-top: 2em;
  }
  &__link{
    align-self: flex-end;
    color: $blue;
    font-size: 0.8rem;
  }
  &__paragraph{
    margin: 0;
  }
}

.article{
  position: relative;
  &__title{
      color: $pink;
      font-size: 1.3em;
      margin-left: 1em;
      @media (max-width: $mobile){
        margin-left: 0;
      }
  }
  &__image{
    float:left; 
    margin:.3em .6em .3em 0;
    @media (max-width: $mobile){
      display: flex;
      float: none;
      margin: 1em auto;
      align-self: center;
    }
  }
  &__footer{
    font-size: .9em;
    font-style: italic;
    margin-bottom: 3em;
    text-align: right;
  }
  &__text{
    text-align: justify;
  }
}

.body{
  background: $pink;
	background: linear-gradient(225deg, rgba(234, 99, 140, 1) 0%, rgba(254, 254, 254, 1) 33%, rgba(254, 254, 254, 1) 83%, rgba(8, 3, 87, 1) 100%);
	color: $violet;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  height: auto;
  min-height: 100%;
  margin-bottom: 1em;
  margin-left: auto;
  margin-right: auto;
  max-width: $desktop;
  min-width: 300px;
  width: auto;
  &__footer{
    bottom: 0;
    color: $magenta;
    font-size: 0.8rem;
    font-style: italic;
    margin-bottom: 1em;
    padding-top: 3em;
    position: absolute;
  }
  &__header{
    background: rgb(234,99,140);
    background: linear-gradient(237deg, rgba(234,99,140,0) 0%, rgba(234,99,140,1) 33%, rgba(234,99,140,1) 66%, rgba(234,99,140,0) 100%);
    box-sizing: border-box;
    left: 0;
    padding: 0.4em;
    position: fixed;
    top: 0;
    width: 100%;
    @media(max-width: $mobile){
      height: 3em;
      padding: 0;
    }
  }
  @media(max-width: $desktop){
    margin: 0 0.5em;
  }
}

.days{
  align-self: stretch;
  display: grid;
  grid-gap: 0.8em;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 3em;
  &__day{
    background: rgba(8, 3, 87, 0.8);
    border-radius: 0.4em;
    color: $magenta;
    display: flex;
    flex-direction: column;
    padding: 0.8em;
  }
  &__header{
    color: $magenta;
    font-size: 0.8em;
    margin: 0;
    text-align: center;
    text-transform: uppercase;
  }
  &__link{
    align-self: flex-end;
    background: $magenta;
    border-radius: 0.4em;
    color: $dark-blue;
    font-size: 0.8rem;
    padding: 0.2em 0.4em;
  }
  &__paragraph{
    background: white;
    border-radius: 0.4em;
    color: $violet;
    margin: 0.8em 0;
    padding: 1em;
  }
  @media(max-width: $mobile){
    grid-template-columns: 1fr;
  }
}

.diary{
  &__header{
    color: $dark-blue;
    margin-bottom: 0.4em;
    margin-top: 2em;
  }
}

.header{
  &__title{
    color: $dark-blue;
    margin-top: 2em;
    text-align: center;
  }
}

.hobby{
  &__header{
    align-self: auto;
    font-weight: bold;
    font-size: 1rem;
    margin: 0.5em 1em;
  }
  &__list-unordered{
    list-style-type: square;
    margin-top: 0;
    margin-left: 2em;
  }
  &__list-ordered{
    list-style-type: georgian;
  }
}

.navigation{
  &__item{
    display: inline;
    margin: 0 0.4em;
  }
  &__link{
    color: $blue;
    font-size: 0.8em;
  }
  &__list{
    align-items: center;
    display: flex;
    justify-content: center;
    list-style: none;
    margin: 0 0.4em;
    padding: 0;
    @media (max-width: $mobile){
      flex-direction: column;
      align-items: baseline;
      display: none;
    }
    &--visible{
      display: flex;
    }
  }
  &__switcher{
    display: none;
    font-size: 2em;
    font-weight: bold;
    position: absolute;
    right: 0.3em;
    top: 0.3em;
    @media (max-width: $mobile){ 
      display: inline-block;
      border: none;
      background: none;
    }
  }
}

.sub-header{
  &__title{
    color: $magenta;
    grid-column: 1;
    margin-top: 2em;
    text-align: center;
    text-transform: uppercase;
  }
}

.week{
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  margin: 1em 0;
  &__header{
    color: $magenta;
    font-size: 1rem;
    font-weight: bolder;
    margin: 1.5em 1em;
    text-transform: uppercase;
  }
  &__link{
    align-self: flex-end;
    color: $blue;
    font-size: 0.8rem; 
  }
  &__paragraph{
    margin: 0 0 1em 0;
  }
}
